import Vue from 'vue'
import VueRouter from 'vue-router'
import FristView from '../views/Frist.vue'
import IntroView from '../views/Intro.vue'
import QnaView from '../views/Qna.vue'
import ResultView from '../views/Result'

import FristView2 from '../views/Frist2'
import IntroView2 from '../views/Intro2'
import QnaView2 from '../views/Qna2'
import ResultView2 from '../views/Result2'

// import ChoiceView from '../views/Choice'

Vue.use(VueRouter)

const routes = [
  {
    path : '/',
    name : 'Choide',
    component :FristView2
  },
  {
    path: '/v2',
    name:'FristView2',
    component: FristView2
  },
  {
    path: '/v2/intro',
    name:'IntroView2',
    component: IntroView2
  },
  {
    path : "/v2/qna",
    name:'QnaView2',
    component: QnaView2
  },
  {
    path : "/v2/result/:id",
    name:'ResultView2',
    component: ResultView2
  },
  
  {
    path: '/v1',
    name:'FristView',
    component: FristView
  },
  {
    path: '/v1/intro',
    name:'IntroView',
    component: IntroView
  },
  {
    path:'/v1/qna_page',
    name:'QnaView',
    component:QnaView
  },
  {
    path:'/v1/result/:id',
    name:'Result_view',
    component:ResultView
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: 'vue_trip',
  routes
})

export default router
