                    ans1_mbit : 'i',
                    ans2 : '여행 가면 필수코스~',
                    ans2_1 : '당연히 해야지! 뭐할까?',
                    ans2_2 : '',
                    ans2_mbit : 'e',
                    display : false
                },
                {
                    num:9,
                    qna_1 : '오늘 저녁은 친구들과 숙소에서',
