        goto_square:function(){
            window.open('https://wordsquare.org/');
        },
        back:function(num){
            if(num==0){
                this.$router.go(-1);
            }else{
                var list = this.qna_list;
                for(var item of list){
                    item.display=false;
                }
                list[num-1].display=true;
                this.res_mbit=this.res_mbit.slice(0,num-1);
            }
        },
        ans_check:function(mbti,num,e){
            var target = $(e.target);
            if(!target.hasClass('answer_item')){
                target.parent('.answer_item').addClass('on');
            }else{
                target.addClass('on');
            }
            setTimeout(() => {
                var list = this.qna_list;
                for(var item of list){
                    item.display=false;
                }
                this.res_mbit.push(mbti);
                if(num+1==12){
                    this.res_calc();
                }else{
                    $('.answer_item').removeClass('on');
                    list[num+1].display=true;
                }
            }, 500);
        },
        res_calc:function(){
            var res_list = this.res_mbit;
            var res_obj={
                e:0,i:0,s:0,t:0,j:0,p:0,n:0,f:0,
            }
            for(var res of res_list){
                switch (res) {
                    case 'i':
                        res_obj.i++;
                        break;
                    case 'e':
                        res_obj.e++;
                        break;
                    case 's':
                        res_obj.s++;
                        break;
                    case 't':
                        res_obj.t++;
                        break;
                    case 'f':
                        res_obj.f++;
                        break;
                    case 'p':
                        res_obj.p++;
                        break;
                    case 'n':
                        res_obj.n++;
                        break;
                    case 'j':
                        res_obj.j++;
                        break;
                    default:
                        break;
                }
            }
            var res_txt="";
            if(res_obj.i>res_obj.e){
                res_txt+="I";
            }else if(res_obj.i<res_obj.e){
                res_txt+="E";
            }
            if(res_obj.s>res_obj.n){
                res_txt+="S";
            }else if(res_obj.s<res_obj.n){
                res_txt+="N";
            }
            if(res_obj.t>res_obj.f){
                res_txt+="T";
            }else if(res_obj.t<res_obj.f){
                res_txt+="F";
            }
            if(res_obj.j>res_obj.p){
                res_txt+="J";
            }else if(res_obj.j<res_obj.p){
                res_txt+="P";
            }
            var mbti_arr=this.mbti_arr;
            var idx=0;
            for(var item of mbti_arr){
                if(item==res_txt){
                    break;
                }
                idx++;
            }
            alert(res_txt);
            this.$router.push('/v2/result/'+idx);
        },
        
    },
}
</script>
